const {
	i18n: { __ },
	components: { SelectControl },
	compose: { withState },
} = wp;

const LibraryAdd = withState( {
	size: '50%',
} )( ( { size, setState } ) => (
	<SelectControl
			label="Size"
			value={ size }
			options={ [
					{ label: 'Big', value: '100%' },
					{ label: 'Medium', value: '50%' },
					{ label: 'Small', value: '25%' },
			] }
			onChange={ ( size ) => { setState( { size } ) } }
	/>
) );

export default LibraryAdd;